html {
	font-size: 100%;
}

body {
	font: 400 100% 'Poppins', sans-serif;
	color: var(--color-neutral-900);
}

h1,
h2,
h3,
h4,
h5,
h6 {
	display: inline-block;
	margin: 0;
	color: var(--color-neutral-900);
}

h1 {
	font-size: 40px;
}

button,
input,
select,
textarea {
	font: 400 1rem 'Poppins', sans-serif;
	color: var(--color-neutral-900);
	transition: color 0.2s ease-in-out, background-color 0.2s ease-in-out, border-color 0.2s ease-in-out;
}

button:hover:not(:disabled) {
	cursor: pointer;
}

.rtbs-button {
	display: inline-flex;
	align-items: center;
	justify-content: center;
	gap: 8px;
	padding: 10px 16px;
	font: 500 0.875rem/20px 'Poppins', sans-serif;
	text-decoration: none;
	border-radius: 8px;
	box-shadow: 0 1px 1px 0.5px rgb(from var(--color-neutral-900) r g b / 0.04),
		0 3px 3px -1.5px rgb(from var(--color-neutral-900) r g b / 0.02),
		0 6px 6px -3px rgb(from var(--color-neutral-900) r g b / 0.04),
		0 12px 12px -6px rgb(from var(--color-neutral-900) r g b / 0.04),
		0 24px 24px -12px rgb(from var(--color-neutral-900) r g b / 0.04),
		0 48px 48px -24px rgb(from var(--color-neutral-900) r g b / 0.04),
		0 0 0 1px rgb(from var(--color-neutral-900) r g b / 0.1),
		inset 0 -1px 1px -0.5px rgb(from var(--color-neutral-900) r g b / 0.06);

	svg {
		display: block;
		width: 20px;
		height: 20px;
	}

	&:has(svg) {
		padding: 10px 16px 10px 12px;
	}

	&--secondary {
		color: var(--color-neutral-900);
		border: none;
		background-image: linear-gradient(to bottom, var(--color-neutral-50), var(--color-neutral-100));
	}
}
